.span_upload_recording{
    font-size: 2rem;
    color: rgb(0, 0, 0);
}
  
.dropzone_upload_recording {
    text-align: center;
    padding: 30px;
    margin-left: auto;
    margin-right: auto;
    margin-top: 3rem;
    border: 3px dashed #6b6868;
    background-color: #fafafa;
    color: #535353;
    height: 13rem;
    width: 30rem;
    font-weight: bold;
    font-size: large;
    
}


.fillButton {
    position: relative;
    height: 45px;
    width: 150px;
    margin: 10px 7px;
    margin-top: 1.5rem;
    padding: 5px 5px;
    font-weight: 700;
    font-size: 15px;
    letter-spacing: 2px;
    color: #ffffff;
    border: 2px #ffffff solid;
    border-radius: 4px;
    outline: 0;
    overflow:hidden;
    background: none;
    z-index: 1;
    cursor: pointer;
    transition:         0.08s ease-in;
    -o-transition:      0.08s ease-in;
    -ms-transition:     0.08s ease-in;
    -moz-transition:    0.08s ease-in;
    -webkit-transition: 0.08s ease-in;
  }
  
  .fill:hover {
    color: rgb(255, 255, 255);
  }
  
  .fill:before {
    content: "";
    position: absolute;
    background: linear-gradient(#72f, #c1b);
    bottom: 0;
    left: 0;
    right: 0;
    top: 100%;
    z-index: -1;
    -webkit-transition: top 0.09s ease-in;
  }
  
  .fill:hover:before {
    top: 0;
  }
  